html {
  font-size: 62.5%;
  @include rmin(1600) {
    font-size: 0.625vw; } }

body {
  background: #FFF;
  color: black;
  line-height: 1.4;
  min-width: 320px;
  @include f(l, 16);
  @include smooth-font; }

a {
  text-decoration: none;
  color: $color-blue;
  @include trans(color);
  &:hover,
  &:focus {
    color: red; } }

.lnk,
a:not([class]) {
  text-decoration: none;
  border-bottom: 1px solid rgba($color-blue, 0.3);
  @include trans();
  transition-property: color, border-color;
  &:hover {
    border-color: rgba(red, 0.3); } }

.lnk_black {
  &:not(:hover) {
    border-color: rgba(black, 0.3); } }

.out {
  overflow: hidden; }

.text {
  @include f(l, 16);
  line-height: 1.5625;
  p {
    margin: 1em 0;
    &:first-child {
      margin-top: 0; }
    &:last-child {
      margin-bottom: 0; } }
  a {
    &:not(:hover) {
      color: inherit; } } }

.header {
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  background: white;
  min-width: 320px;
  transition: box-shadow 0.3s ease;
  .hero-links {
    margin: 0;
    position: absolute;
    top: 0;
    height: 100%;
    left: 0;
    right: 0;
    opacity: 0;
    background: white;
    visibility: hidden;
    transition: all 0.2s ease-out;
    overflow: hidden;
    &:before {
      content: "";
      display: inline-block;
      vertical-align: middle;
      height: 100%;
      width: 0; } }
  .hero-btn {
    transform: translateY(100%);
    transition: all 0.2s ease-out; }
  &.has-shadow {
    box-shadow: 0 0 10px 0 rgba(black, 0.3); }
  &.is-show-buttons {
    .hero-links {
      opacity: 1;
      visibility: visible; }
    .hero-btn {
      transform: translateY(0); }
    .lang {
      z-index: 0; } }
  &.is-hide-buttons {
    .hero-links {
      opacity: 0;
      visibility: hidden;
      transition-timing-function: ease-in; }
    .hero-btn {
      transform: translateY(-100%);
      transition-timing-function: ease-in; } }
  @include sm {
    .l {
      position: relative; }
    .header__inner {
      padding: 15px 0;
      position: static; }
    .hero-links {
      padding-left: 15px;
      padding-right: 15px; }
    .lang {
      top: 50%;
      margin-top: -0.65em;
      right: 70px; }
    .nav {
      visibility: hidden;
      opacity: 0;
      position: absolute;
      z-index: 3;
      top: 0;
      right: 0;
      left: 0;
      background: white;
      text-align: center;
      padding: 40px 15px 15px;
      box-shadow: 0 0 10px 0 rgba(black, 0.3);
      transition: 0.3s ease;
      transition-property: opacity, visibility;
      max-height: 100vh;
      overflow: auto;
      box-sizing: border-box;
      @include smooth-scroll; }
    .nav__item {
      display: block;
      margin: 0; }
    .nav__link {
      line-height: 2; }
    &.is-show-menu {
      .logo {
        z-index: 5; }
      .nav {
        opacity: 1;
        visibility: visible; } } }
  @include sm {
    .hero-btn {
      font-size: 14px; } } }

.header__inner {
  position: relative;
  padding: 22px 0; }

.header__buttons {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow: hidden; }

.menu-button {
  display: none;
  position: absolute;
  z-index: 5;
  top: 50%;
  right: 8px;
  margin-top: -20px;
  width: 40px;
  height: 40px;
  box-sizing: border-box;
  padding: 8px;
  color: $color-blue;
  background: none;
  &:before,
  &:after,
  > span {
    content: "";
    display: block;
    width: 100%;
    border-top: 3px solid;
    transition: all 0.3s ease; }
  > span {
    margin: 6px 0; }
  .is-show-menu & {
    &:before {
      transform: rotate(45deg);
      transform-origin: 2px 6px; }
    &:after {
      transform: rotate(-45deg);
      transform-origin: 1px -3px; }
    > span {
      opacity: 0;
      transform: scale(0); } }
  &:hover {
    color: red; }
  @include sm {
    display: block; } }

.lang {
  position: absolute;
  z-index: 1;
  top: 25px;
  // right: -33px
  right: 0;
  @include f(l, 16);
  color: $color-grey;
  a {
    border: none;
    &:not(:hover) {
      color: inherit; } }
  &:hover,
  &.is-hover {
    .lang__list {
      opacity: 1;
      visibility: visible; } }
  @include md {
    right: 0; } }

.lang__list {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  z-index: 1;
  top: -10px;
  left: -15px;
  min-width: 100%;
  box-sizing: content-box;
  padding: 10px 15px;
  white-space: nowrap;
  background: white;
  box-shadow: 0 0 10px 0 rgba(black, 0.3);
  @include trans; }

.nav {
  position: relative; }

.nav__item {
  display: inline-block;
  vertical-align: top;
  margin-right: 20px;
  line-height: 1.7; }

.nav__link {
  display: inline-block;
  @include f(l, 16); }

.logo {
  display: inline-block;
  position: absolute;
  top: 50%;
  margin-top: -0.55em;
  right: 100%;
  margin-right: percentage(75/1200);
  color: black;
  @include f(b, 16, 100);
  text-decoration: none;
  text-transform: uppercase;
  line-height: 1;
  @include r(1400) {
    margin-right: percentage(45/1200); }
  @include lg {
    position: relative;
    top: auto;
    right: auto;
    margin: 0; } }

.link {
  color: $color-blue;
  text-decoration: none;
  border-bottom: 1px dotted;
  line-height: 1;
  cursor: pointer;
  @include trans(color);
  &:hover {
    color: red; }
  &_solid-line {
    border-style: solid;
    border-color: rgba($color-blue, 0.3);
    &:hover {
      border-color: rgba(red, 0.3); } } }

.toparea {
  padding-top: 130px;
  padding-bottom: 95px;
  overflow: hidden;
  @include sm {
    padding-top: 100px;
    padding-bottom: 40px; } }

.hero {
  position: relative;
  @include f(el, 16);
  line-height: 1.526;
  padding: 0 45% 0 0;
  p {
    margin: 1em 0; }
  .btn {
    margin-top: 15px; }
  @include sm {
    padding-right: 0;
    .hero-figure {
      display: none; } } }

.hero-figure {
  position: absolute;
  z-index: -1;
  top: -30px;
  right: 0;
  visibility: hidden;
  opacity: 0;
  width: 420px;
  > svg {
    width: 100%;
    .dashed-line {
      stroke-dasharray: 3px !important;
      transition: stroke-dasharray 0.5s linear; } }
  &-iptv {
    .hero-figure__text {
      width: 330px; } }
  &-ipcam {
    top: -24px;
    right: 40px;
    .hero-figure__text {
      width: 380px; } }
  &.is-active {
    visibility: visible;
    opacity: 1; }
  @include md {
    top: -20px;
    right: -30px; } }

.hero-figure__text {
  position: absolute;
  top: 320px;
  right: 0;
  @include f(el, 14);
  line-height: 1.43;
  color: $color-blue; }

.hero-links {
  margin: 23px 0; }

.hero-text {
  display: none;
  &.is-active {
    display: block; } }

.hero-btn {
  display: inline-block;
  margin-right: 15px;
  text-decoration: none;
  @include f(el, 16);
  color: inherit;
  @include trans(color);
  line-height: 1;
  color: $color-blue;
  cursor: pointer;
  user-select: none;
  > * {
    display: inline-block;
    vertical-align: middle; }
  span {
    border-bottom: 1px dotted; }
  g {
    stroke: currentColor; }
  circle {
    visibility: hidden;
    opacity: 0;
    @include trans; }
  &:hover {
    color: red; }
  &.is-active {
    color: black;
    cursor: default;
    span {
      border-color: rgba(black, 0); }
    circle {
      visibility: visible;
      opacity: 1; } } }


.title {
  @include f(el, 50);
  line-height: 1.1;
  &_bold {
    @include f(l); }
  @include md {
