@keyframes cloud {
  0%, 100% {
    opacity: 1;
    transform: translate3d(0,0,0); }
  50% {
    opacity: 0;
    transform: translate3d(50px,0,0); }
  51% {
    opacity: 0;
    transform: translate3d(-50px,0,0); } }

.transcoder {
  right: 50%;
  margin-right: percentage(255/1200);
  top: 465px;
  width: 233px;
  height: 400px;
  background: img-url('transcoder.png') no-repeat center;
  background-size: contain;
  &__lightning {
    width: 100px;
    height: 100px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -47px 0 0 -44px; }
  @include md {
    margin-right: 200px; }
  @include rmin(1800) {
    margin-right: percentage(240/1200); } }

.hdd {
  top: 495px;
  left: 50%;
  margin-left: percentage(210/1200);
  @include image(395,540,hdd);
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    @include image(395,540,hdd-lights);
    animation: blink 2s ease-in-out infinite; }
  @include md {
    margin-left: 196px; } }

@keyframes blink {
  0%, 100% {
    opacity: 0; }
  50% {
    opacity: 1; } }


.lego {
  top: 300px;
  left: 50%;
  margin-left: percentage(218/1200);
  &__brick-upper {
    position: absolute;
    z-index: 1;
    top: -92px;
    left: 65px;
    @include image(267,202,lego-blue);
    transform: translateY(-100px);
    transition: transform 1.5s ease; }
  &__brick-lower {
    position: relative;
    z-index: 0;
    @include image(267,202,lego-orange); }
  &.animate {
    .lego__brick-upper {
      transform: translateY(0); } }
  @include md {
    margin-left: 200px; } }

.form {
  .btn {
    margin-top: 44px; }
  @include sm {
    .btn {
      margin-top: 20px; } } }

.field {
  position: relative;
  margin: 20px 0;
  &:first-child {
    margin-top: 0; }
  &:last-child {
    margin-bottom: 0; } }

.field__label {
  position: absolute;
  top: em(11,16);
  left: 0;
  @include f(l, 16, 150);
  text-transform: uppercase;
  color: #9D9CA7; }

.input {
  display: block;
  box-sizing: border-box;
  height: 40px;
  height: 4rem;
  padding: 10px 0 9px 120px;
  padding: 1rem 0 0.9rem 12rem;
  width: 100%;
  appearance: none;
  background: none;
  border: none;
  border-bottom: 1px solid rgba(black, 0.5);
  @include f(r, 20);
  color: black;
  resize: none;
  border-radius: 0;
  @include placeholder {
    @include f(l, 16, 150);
    text-transform: uppercase;
    color: #9D9CA7; } }

textarea.input {
  max-height: 200px;
  line-height: 1.3;
  padding-top: 0.7rem;
  padding-bottom: 0.6rem;
  @include smooth-scroll; }


.news {
  line-height: 1.5625;
  &:not(:last-child) {
    margin-bottom: 25px; }
  &_important {
    @include rmin(767) {
      .news__title {
        font-size: 25px;
        font-size: 2.5rem; }
      .news__text {
        font-size: 22px;
        font-size: 2.2rem;
        margin: 10px 0; }
      .news__date {
        font-size: 16px;
        font-size: 1.6rem; } } } }

.news__title {
  @include f(r); }

.news__date {
  display: block;
  font-size: 14px;
  font-size: 1.4rem;
  margin-top: 5px;
  color: $color-grey; }


.message {
  display: block;
  margin-top: 35px;
  padding: 10px;
  padding: 1rem;
  box-sizing: border-box;
  text-align: center;
  @include f(l, 16);
  line-height: 1.6;
  background: #8CFFB7;
  &_error {
    background: transparent; }
  border-radius: 2px; }

.customers {
  @include sm {
    .customers__list {
      column-count: 2; } }
  @include xs {
    .customers__list {
      margin: 0;
      column-count: initial;
      column-gap: initial; }
    .customers__list-item {
      display: block;
      width: auto;
      margin: 20px 0 0 0;
      padding: 0; } } }

.customers__list {
  font-size: 0;
  margin: 0 0 -50px;
  column-count: 3;
  column-gap: 60px; }

.customers__list-item {
  display: inline-block;
  vertical-align: top;
  font-size: 16px;
  font-size: 1.6rem;
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 50px; }

.customer {
  &:hover {
    .customer__pic {
      filter: grayscale(0);
      opacity: 1; }
    .customer__link {
      color: red;
      border-color: rgba(red, 0.3); } }
  @include sm {
    .customer__pic {
      height: auto;
      max-height: 60px;
      line-height: normal; } } }

.customer__pic {
  height: 60px;
  height: 6rem;
  margin-bottom: 10px;
  margin-bottom: 1rem;
  line-height: 60px;
  line-height: 6rem;
  font-size: 0;
  filter: grayscale(1);
  opacity: 0.4;
  @include gpu;
  @include trans;
  a {
    display: inline-block;
    height: 100%;
    border: none;
    text-decoration: none;
    max-width: 100%; }
  img {
    display: inline-block;
    vertical-align: middle;
    max-height: 60px;
    max-width: 100%; } }

.customer__link {
  display: inline-block;
  @include f(l, 12);
  &:not(:hover) {
    color: $color-grey;
    border-color: rgba($color-grey, 0.3); } }

.icon-link {
  display: inline-block;
  vertical-align: top;
  margin-right: 40px;
  color: $color-blue;
  text-decoration: none;
  line-height: 1.375;
  @include trans(color);
  .svg-icon {
    display: block;
    font-size: 40px;
    font-size: 4rem;
    fill: currentColor;
    margin-bottom: 5px; }
  .lnk {
    @include trans(border-color); }
  &:hover {
    color: red;
    .lnk {
      border-color: rgba(red, 0.3); } } }

.social-link {
  display: inline-block;
  vertical-align: middle;
  text-decoration: none;
  margin-right: 20px;
  text-align: center;
  color: inherit;
  @include trans;
  transition-property: color, opacity;
  &:last-child {
    margin-right: 0; }
  .svg-icon {
    fill: currentColor;
    display: block;
    margin: auto; }
  .svg-facebook {
    font-size: 32px;
    font-size: 3.2rem; }
  .svg-twitter {
    font-size: 24px;
    font-size: 2.4rem; }
  .svg-youtube-play {
    font-size: 20px;
    font-size: 2rem; }
  &:hover {
    color: inherit;
    opacity: 0.8; } }

.hidden-text {
  @include hide-text; }

.map {
  height: 445px;
  height: 44.5rem;
  // border: 1px solid rgba($color-blue, 0.3)
  // border-width: 1px 0
  @include sm {
    height: 250px; } }

.map__placeholder {
  height: 100%;
  background: img-url('map.jpg') no-repeat center;
  background-size: cover; }

.footer {
  padding: 15px 15px 20px;
  color: $color-grey;
  @include f(l, 12);
  line-height: 1.5;
  border-top: 1px solid rgba($color-grey, 0.3);
  a {
    &:not(:hover) {
      color: inherit;
      border-color: rgba($color-grey, 0.3); } }
  @include clr;
  @include xs {
    text-align: center;
    .footer__left {
      float: none;
      margin-top: 10px; }
    .footer__right {
      float: none;
      width: auto; }
    .site-maker {
      left: 0; } } }

.footer__left {
  float: left; }

.footer__right {
  float: right;
  width: 50%; }

.partners {
  padding: 29px 0 33px;
  text-align: center; }

.partners__title {
  margin-bottom: 16px;
  @include f(l, 12, 180);
  line-height: 1;
  text-transform: uppercase;
  color: $color-blue; }

.partners__item {
  display: inline-block;
  vertical-align: middle;
  opacity: 0.4;
  filter: grayscale(1);
  @include trans();
  &:not(:last-child) {
    margin-right: 25px; }
  &:hover {
    filter: grayscale(0);
    opacity: 1; }
  a {
    border: none; } }

.partner-logo {
  display: inline-block;
  vertical-align: middle;
  opacity: 0.6;
  cursor: pointer;
  @include trans();
  margin: 10px 10px 0 0;
  > img {
    display: block; }
  &:hover {
    opacity: 1; } }


.site-maker {
  @include clr;
  min-width: 278px;
  position: relative;
  left: -45px;
  > img,
  .logo-als {
    float: left;
    margin: 4px 12px 0 0; }
  @include xs {
    > img,
    .logo-als {
      float: none;
      display: block;
      margin: 0 auto 10px; } } }

